import Slang_Toggle_Front from '$lib/trinkets/Slang/Toggle_Front.svelte'

const modal_store = getModalStore ();
const mode = check_roomies_truck ().freight.mode;		

let Milieus_Freight = false;

const keyframes = [];
let flickerEl;

const Embark_Animation = () => {
	console.info ("Embark_Animation:", flickerEl);
	
	const totalFrames = 60;
	const totalDuration = 30000;

	for (let i = 0; i <= totalFrames; i++) {
		const t = i / totalFrames;
		const flicker = 0.8 + 0.2 * Math.sin (2 * Math.PI * t * 5 + Math.sin(2 * Math.PI * t * 3));
		
		keyframes.push ({
			opacity: flicker,
			duration: totalDuration / totalFrames
		});
	}

	anime ({
		targets: flickerEl,
		keyframes,
		duration: totalDuration,
		loop: true,
		easing: 'linear'
	});
}



</script>


